<template>
  <div>
    <el-row :gutter="10">
      <el-col :xs="24" :sm="16" >
   
          <el-input v-model="typeText" placeholder="请输入待办事项" />
       
      </el-col>
      <el-col :xs="22" :sm="7">
      
          <el-date-picker
            v-model="timeValue"
            size="large"
            type="datetime"
            placeholder="选择完成日期时间"
            default-time="12:00:00"
            :picker-options="pickerOptions"
          >
          </el-date-picker>
      
      </el-col>
      <el-col :xs="2" :sm="1" class='plus-icon'>
      
          <i
            class="el-icon-plus"
            v-on:click="emit"
            v-loading="isLoading"
            element-loading-spinner="el-icon-loading"
          ></i>
     
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: "Type",
  props: ["isLoading"],
  data: function () {
    return {
      typeText: "",
      pickerOptions: {
        disabledDate(date) {
          const now = new Date();
          now.setSeconds(now.getSeconds() + 40);
          return date > new Date().setSeconds(0) ? false : true;
        },
      },
      timeValue: "",
    };
  },
  methods: {
    emit: function () {
      const text = this.typeText;
      this.$emit("type-text", { text: text, date: this.timeValue });
      this.typeText = "";
      this.timeValue="";
      
    },
  },
};
</script>

