<template>
  <el-card
    v-bind:class="{ starGrey: item.status === 'starred' }"
    shadow="hover"
  >
    <el-row :gutter="10" type="flex">
      <el-col :span="1">
        <i class="el-icon-circle-check" v-on:click="deleteItem"></i>
      </el-col>
      <el-col :span="11" v-if="isClicked">
        <el-input class="title-edit" v-model="item.title" />
      </el-col>
      <el-col :span="11" v-if="!isClicked">
        <span class="itemValue">{{ item.title }}</span>
      </el-col>
      <el-col :span="9" v-if="!isClicked">
        <span class="dueTime">{{ formatDate(item.due_date) }}</span>
      </el-col>
      <el-col :span="9" v-if="isClicked">
        <el-date-picker
          v-model="selectedDate"
          size="large"
          type="datetime"
          placeholder="选择完成日期时间"
          default-time="12:00:00"
          :picker-options="pickerOptions"
        >
        </el-date-picker>
      </el-col>
      <el-col :span="2">
        <i
          v-bind:class="[isClicked ? 'el-icon-check' : 'el-icon-edit']"
          v-on:click="edit"
        ></i>
      </el-col>
      <el-col :span="1">
        <i
          v-bind:class="[
            item.status === 'starred' ? 'el-icon-star-on' : 'el-icon-star-off',
          ]"
          v-on:click="toggleStar"
        ></i>
      </el-col>
    </el-row>
  </el-card>
</template>

<script>
export default {
  name: "Singlelist",
  data: function () {
    return {
      isClicked: false,
      typeText: "",
      pickerOptions: {
        disabledDate(date) {
          const now = new Date();
          now.setSeconds(now.getSeconds() + 40);
          return date > new Date().setSeconds(0) ? false : true;
        },
      },
      selectedDate: null,
    };
  },
  props: ["item"],
  methods: {
    deleteItem: function () {
      this.$emit("delete-item", this.item.id);
    },
    toggleStar: function () {
      this.$emit("toggle-star", this.item);
    },
    edit() {
      if (this.isClicked) {
        console.log('due time');
        this.item.due_date = this.selectedDate ? this.selectedDate.getTime() : 0;
        fetch("/api/item", {
          body: JSON.stringify({ id: this.item.id, title: this.item.title, due_date: this.item.due_date}),
          method: "PUT",
        })
          .then((r) => r.json())
          .then((r) => {
            if (!r.success) {
              this.$message("网络异常");
            }
          });
      } else {
        if(this.item.due_date !== 0) {         
          this.selectedDate = new Date(this.item.due_date);
        }
      }
      this.isClicked = !this.isClicked;
    },
    formatDate(n) {
      if (n === 0) {
        return "--";
      }
      let now = Date.now();
      let diff = n - now;
      let second = 1000;
      let min = 1000 * 60;
      let hour = min * 60;
      let day = hour * 24;
      let mouth = 30.5 * day;
      let year = 365 * day;

      if (diff / year > 1) {
        let n = parseInt(diff / year);
        diff = diff - parseInt(diff / year) * year;
        return n.toString() + "年后";
      } else if (diff / mouth > 1) {
        let n = parseInt(diff / mouth);
        diff = diff - parseInt(diff / mouth) * mouth;
        return n.toString() + "月后";
      } else if (diff / day > 1) {
        let n = parseInt(diff / day);
        diff = diff - parseInt(diff / day) * day;
        return n.toString() + "日后";
      } else if (diff / hour > 1) {
        let n = parseInt(diff / hour);
        diff = diff - parseInt(diff / hour) * hour;
        return n.toString() + "小时后";
      } else if (diff / min > 1) {
        let n = parseInt(diff / min);
        diff = diff - parseInt(diff / min) * min;
        return (
          n.toString() + "分钟后" + parseInt(diff / 1000).toString() + "秒后"
        );
      } else if (diff / second > 1) {
        let n = parseInt(diff / second);
        diff = diff - parseInt(diff / second) * second;
        return n.toString() + "秒后";
      } else if (diff < 0) {
        return "已到期";
      }

      // return new Date(n).toString();
    },
  },
};
</script>

