<template>
  <div>
    <Type v-on:type-text="getInput" v-bind:isLoading="isAddBtnLoading"></Type>
    <!-- <Singlelist v-bind:item="obj"></Singlelist> -->
    <transition-group name="list" tag="p">
      <Singlelist
        v-for="item in sortedItems"
        v-bind:key="item.id"
        v-bind:item="item"
        v-on:delete-item="deleteList"
        v-on:toggle-star="changeStatus"
        v-loading="isListLoading"
      ></Singlelist>
    </transition-group>
    <div v-if="items.length === 0">点击右上角+创建新的待办</div>
  </div>
</template>

<script>
import Type from "./Type";
import Singlelist from "./Singlelist";
import { itemSort } from "../Utils";

export default {
  name: "Todolist",
  data: function () {
    return {
      items: [],
      isListLoading: false,
      isAddBtnLoading: false,
    };
  },
  methods: {
    getInput: function (i) {
      console.log("getInput", i);
      if (i.text.length === 0) {
        this.$message("添加内容不可为空");
      }  else {
          this.isAddBtnLoading = true;
          fetch("/api/item", {
            body: JSON.stringify({
              title: i.text,
              create_date: Date.now(),
              due_date: i.date ? i.date.getTime() : 0,
            }),
            method: "POST",
          })
            .then((r) => r.json())
            .then((r) => {
              this.isAddBtnLoading = false;
              this.items.unshift(r.data);
            });
        
      }

      console.log(this.items);
    },
    deleteList: function (id) {
      const index = this.items.findIndex((item) => {
        return item.id === id;
      });

      fetch(`/api/item/${id}`, { method: "DELETE" })
        .then((r) => r.json())
        .then((r) => {
          console.log(r.data);
          if (r.success != true) {
            this.$message(`${r.message}`);
          } else {
            this.items.splice(index, 1);
          }
        });
    },
    changeStatus: function (item) {
      if (item.status === "starred") {
        item.status = "normal";
      } else {
        item.status = "starred";
      }
      fetch("/api/item", {
        body: JSON.stringify({ id: item.id, status: item.status }),
        method: "PUT",
      })
        .then((r) => r.json())
        .then((r) => {
          if (!r.success) {
            this.$message("网络异常");
          }
        });
    },
  },
  mounted: function () {
    console.log("mounted");
    this.isListLoading = true;
    fetch("/api/item_list")
      .then((r) => r.json())
      .then((r) => {
        this.isListLoading = false;
        this.items = r.data;
      });
  },
  components: {
    Type,
    Singlelist,
  },
  computed: {
    sortedItems: function () {
      return itemSort(this.items);
    },
  },
};
</script>

