export function itemSort(list) {
    list.sort((a, b)=>{
        if(a.status === b.status) {
          return b.create_date - a.create_date;
        } else {
          if (a.status === 'starred') {
            return -1;
          } else {
            return 1;
          }
        }
    });
    return list;
}